<template>
    <div class='ziliao'>
        <div class="title">{{$t('ziliao[0]')}}</div>
        <div class="hr"></div>
        <div class="item">
            <div>{{$t('ziliao[1]')}}：</div>
            <div>{{userInfo.name}}</div>
        </div>
        <div class="item">
            <div>{{$t('ziliao[2]')}}：</div>
            <div>{{userInfo.real_name ? userInfo.real_name : '----'}}</div>
        </div>
        <div class="item">
            <div>{{$t('ziliao[3]')}}：</div>
            <div>{{userInfo.sex}}</div>
        </div>
        <div class="item">
            <div>{{$t('ziliao[4]')}}：</div>
            <div>{{userInfo.email}}</div>
        </div>
        <div class="itemPhone item">
            <div>{{$t('ziliao[5]')}}：</div>
            <div>{{userInfo.phone}}</div>
            <!-- <div>修改手机</div> -->
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            userInfo:'',
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        // _this.userInfo = JSON.parse(sessionStorage.userInfo
        _this.apis.get(_this.apis.get_user_info,{params:{lang:_this.apis.lang}}).then(function(res) {
            if(res.data.code == 0 ){
                _this.userInfo = res.data.data
            }
        })
    },
    watch:{
    },
    methods:{
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.ziliao{
    min-height: 631px;
    border: 1px solid #CACACA;
    padding: 20px 40px;
    margin-bottom: 100px;
    .hr{
        width: 98%;
        height: 1px;
        margin-left: 2%;
        margin-top: 25px;
        margin-bottom: 25px;
        background: #DEDEDE;
    }
    .title{
        color: #383838;
        font-size: 13px;
        font-weight: bold;
    }
    .item{
        display: flex;
        align-items: center;
        font-weight: bold;
        margin: 13px 0;
        &>div:nth-child(1){
            color: #383838;
            font-size: 13px;
            width: 100px;
            padding-left: 20px;
        }
        &>div:nth-child(2){
            flex: 1;
            color: #383838;
            font-size: 13px;
            font-weight: 100;
        }
    }
    .itemPhone{
        display: flex;
        align-items: center;
        font-weight: bold;
        margin: 13px 0;
        &>div:nth-child(1){
            color: #383838;
            font-size: 13px;
            width: 100px;
            padding-left: 20px;
        }
        &>div:nth-child(2){
            width: 130px;
            color: #383838;
            font-size: 13px;
        }
        // &>div:nth-child(3){
        //     flex: 1;
        //     color: #FF8502;
        //     font-size: 13px;
        //     font-weight: normal;
        // }
    }
}
</style>


